import './monav.scss'
const MoNav = {
    props:['show'],
    template:`
    <transition name='fade'>
        <div class='mo_nav' v-if="show">
            <div class='inner'>
                <ul>
                    <li>
                        <router-link to='/about' tag='span'> </router-link>
                    </li>
                    <li>
                        <router-link to='/product' tag='span'> </router-link>
                        <div class='sub_menu'>
                            <router-link to='/product/pro' tag='p'>Win-Win Pro</router-link>    
                            <router-link to='/product/pos' tag='p'>Win-Win Pos</router-link>    
                        </div>
                    </li>
                    <li>
                        <span></span>
                    <div class='sub_menu'>
                        <p> </p>
                        <p> </p>
                        <p></p>
                        </div>
                    </li>
                    <li></li>
                </ul>
            </div>
        </div>
    </transition>`

}

export default MoNav;